.layer3 {
  width: 100%;
  height: 100%;
  z-index: 100;
}

.bottomText {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
  height: calc(100% - 1rem);
  z-index: 100;
  p {
    width: 90%;
  }
}

.cardWrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: 84%;
  width: 100%;
}

.card {
  color: white;
  box-shadow: 1.25rem 1.25rem 0 0 #4e4e4e;
  display: flex;
  flex-direction: column;
  padding: 0.5rem;
  margin-bottom: 0.5rem;
  justify-content: space-evenly;
  div {
    padding: 0.15rem;
  }
}

.mobile {
  display: none;
}

.nonMobile {
  display: none;
}

.card1 {
  @extend .card;
  background-color: #dd6969;
}

.card2 {
  @extend .card;
  background-color: #689a81;
}

.card3 {
  @extend .card;
  background-color: #6071aa;
}

@media all and (min-width: 801px) {
  .mobile {
    display: none !important;
  }

  .nonMobile {
    display: block !important;
  }

  .card {
    max-width: 28rem;
    min-height: 7.5rem;
    p {
      font-size: 0.8rem !important;
    }
  }
  .card1 {
    margin-left: calc(10%);
  }
  .card2 {
    margin-top: -1rem;
    margin-left: calc(50% - 14rem);
  }
  .card3 {
    margin-top: -1rem;
    margin-left: calc(90% - 28rem);
  }

  .slide3Title {
    margin-top: 2rem;
  }
}

@media all and (max-width: 800px) {
  .mobile {
    display: block !important;
  }

  .nonMobile {
    display: none !important;
  }

  .card {
    margin-left: calc(10% - 0.625rem);
    max-width: 80%;
    p {
      font-size: 0.85rem !important;
    }
  }
  .slide3Title {
    margin-top: 1rem;
  }
}

.cardText {
  margin-top: 1em;
  width: 80%;
}

.container {
  position: relative;
  z-index: 100;
  margin-left: 8%;
}

.slide3Title {
  color: black !important;
}

.cardContainer {
  color: white;
  height: 11rem;
  width: 30%;
  position: absolute;
  background-color: #dd6969;
  padding: 1rem;
  box-shadow: 20px 20px 0 0 #4e4e4e;
  //font-size: 0.9rem;
  z-index: 100;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.card1Container {
  @extend .cardContainer;
  left: 2%;
  top: 30%;
}

.card2Container {
  @extend .cardContainer;
  left: 25%;
  top: 45%;
  background-color: #689a81;
}

.card3Container {
  @extend .cardContainer;
  left: 48%;
  top: 60%;
  background-color: #6071aa;
}

@media all and (max-width: 900px) {
  .windows {
    margin-left: 0;
  }

  .cardContainer {
    width: 80%;
  }

  .card1Container {
    left: 10%;
    top: 20%;
  }

  .card2Container {
    left: 10%;
    top: 40%;
    background-color: #70117c;
  }

  .card3Container {
    left: 10%;
    top: 60%;
    background-color: #83788b;
  }

  .slide3Title {
    color: white;
    position: absolute;
    left: 0;
    //top: 10%;
  }
}

.cloud1 {
  z-index: 10;
  margin-left: calc(56%);

  img {
    width: 14rem;
  }
}

.cloud3 {
  z-index: 10;
  margin-left: calc(100% - 20rem);

  img {
    width: 16rem;
  }
}

.aircraft {
  z-index: 11;
  margin-left: calc(56% + 6.6rem);
  margin-top: 2.2rem;
  img {
    width: 9rem;
  }
}

.aircraft_bottom {
  @extend .aircraft;
  margin-left: calc(10% + 6.6rem);
}

.cloud1_bottom {
  @extend .cloud1;
  margin-left: calc(10%);
}

.cloud3_bottom {
  @extend .cloud3;
  margin-left: 25%;
}

@media all and (max-width: 901px) {
  .cloud1,
  .aircraft {
    opacity: 0;
  }
  .cloud3 {
    margin-top: -2.5rem;
  }
}
