.windows {
  position: relative;
  z-index: 100;
  margin-left: 8%;
}

@media all and (max-width: 800px) {
  .windows {
    margin-left: -1rem;
  }
  .rocket2 {
    opacity: 0;
  }
}

.window {
  position: absolute;
}

.comet1 {
  z-index: 10;
  margin-left: 3rem;

  img {
    width: 6.5rem;
    height: 6.5em;
  }
}

.comet2 {
  z-index: 10;
  margin-left: calc(60%);

  img {
    width: 7 * 0.88rem;
    height: 7rem;
  }
}

.rocket1 {
  z-index: 10;
  margin-left: calc(95% - 8rem);

  img {
    width: 10 * 1.27rem;
    height: 10rem;
  }
}

.planet10 {
  z-index: 10;
  margin-left: calc(25%);

  img {
    width: 5 * 1.75rem;
    height: 5rem;
  }
}

.planet11 {
  z-index: 10;
  margin-left: calc(73%);

  img {
    width: 6 * 0.95rem;
    height: 6rem;
  }
}

.planet12 {
  z-index: 10;
  margin-left: calc(98% - 8rem);

  img {
    width: 7.5rem;
    height: 7.5rem;
  }
}

.rocket2 {
  z-index: 10;
  margin-left: calc(90% - 20rem);

  img {
    width: 12rem;
    height: 12rem;
  }
}
